.header-title {
  font-size: 48px;
  margin: 25px 0px; }

.main-pause-play-btn {
  background: #1db954;
  font-family: 'Proxima Nova', Georgia, sans-serif;
  color: #fff;
  border: none;
  border-radius: 20px;
  padding: 10px;
  width: 100px;
  font-size: 14px;
  letter-spacing: 1px;
  outline: none;
  -webkit-transition: background-color 200ms ease-in-out;
  -ms-transition: background-color 200ms ease-in-out;
  transition: background-color 200ms ease-in-out; }
  .main-pause-play-btn:hover {
    background: #21cc5d; }
  .main-pause-play-btn:active {
    background: #19a74b; }
