.album-container {
  max-width: initial !important;
  width: 100%; }
  @media (max-width: 900px) {
    .album-container {
      margin: 0 auto;
      width: 80%; } }
  .album-container .add-remove-section .fa {
    margin-top: 9px !important;
    margin-left: 4px !important;
    text-align: center; }
  .album-container .cd-header {
    line-height: 40px;
    padding-left: 17px;
    font-size: 15px;
    color: #ccc; }
  .album-container .song-number-header {
    width: 58px !important; }
    .album-container .song-number-header p {
      font-family: 'Proxima Thin', Georgia, sans-serif;
      text-transform: uppercase;
      font-size: 12px;
      color: #ccc;
      padding: 0 15px; }
      .album-container .song-number-header p:hover {
        color: #fff; }
  .album-container .song-number-header,
  .album-container .r-song {
    width: 40px;
    text-align: center; }
  .album-container .song-title-header,
  .album-container .song-title {
    width: 40% !important;
    margin-left: 0px; }
  .album-container .song-artist-header,
  .album-container .song-artist {
    width: 30% !important; }
  .album-container .explicit-header,
  .album-container .song-explicit,
  .album-container .song-length-header,
  .album-container .song-length {
    text-align: center;
    width: 100px; }
    @media (max-width: 800px) {
      .album-container .explicit-header,
      .album-container .song-explicit,
      .album-container .song-length-header,
      .album-container .song-length {
        display: none; } }
  .album-container .user-song-item .fa:active {
    color: #aaa; }
  .album-container .user-song-item:hover .r-song {
    padding-top: 5px; }
    .album-container .user-song-item:hover .r-song .fa:first-of-type {
      display: inline; }
    .album-container .user-song-item:hover .r-song span {
      display: none; }
  .album-container .user-song-item.active .add-remove-section .fa {
    display: inline !important; }
  .album-container .user-song-item.active .r-song {
    padding-top: 5px; }
    .album-container .user-song-item.active .r-song .fa:first-of-type {
      display: none; }
    .album-container .user-song-item.active .r-song .fa:last-of-type {
      display: inline; }
  .album-container .user-song-item.active:hover .fa:first-of-type {
    display: inline; }
  .album-container .user-song-item.active:hover .fa:last-of-type {
    display: none; }
