.search-container {
  min-height: 500px;
  height: 100%; }
  .search-container .table-container {
    display: table;
    width: 100%;
    height: 100%; }
  .search-container .search-index {
    position: fixed;
    height: 100%;
    text-align: center;
    color: #ccc;
    left: 0;
    right: 0;
    top: 35%; }
    .search-container .search-index .fa {
      font-size: 60px;
      -webkit-text-stroke: 6px #181818; }
    .search-container .search-index .fa-flag-o {
      -webkit-text-stroke: 3px #181818; }
    .search-container .search-index h2 {
      color: white; }
    .search-container .search-index span {
      font-size: 14px; }
    @media (max-width: 1100px) {
      .search-container .search-index {
        left: 220px; } }
    @media (max-width: 700px) {
      .search-container .search-index {
        left: 0px; } }
  .search-container .results-table {
    display: table-cell;
    vertical-align: middle;
    margin-top: 10px; }
  .search-container .search-results {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-bottom: 80px; }
    .search-container .search-results .search-result {
      max-width: 500px;
      width: 450px;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px; }
      @media (max-width: 600px) {
        .search-container .search-results .search-result {
          width: 300px; } }
      .search-container .search-results .search-result .result-header {
        width: 100%;
        border-bottom: 1px solid #676666;
        height: 50px; }
        .search-container .search-results .search-result .result-header h4 {
          display: inline-block;
          width: 50%;
          margin-bottom: 5px; }
        .search-container .search-results .search-result .result-header span {
          width: 50%;
          font-size: 14px;
          text-align: right;
          display: inline-block;
          color: #ccc; }
          .search-container .search-results .search-result .result-header span:hover {
            text-decoration: underline;
            color: white; }
      .search-container .search-results .search-result .result-body {
        display: flex;
        flex-wrap: wrap;
        width: 100%; }
        .search-container .search-results .search-result .result-body .item {
          display: flex;
          padding-left: 0px;
          width: 48%;
          height: 60px; }
          .search-container .search-results .search-result .result-body .item:nth-child(even) {
            margin-left: 5px; }
          .search-container .search-results .search-result .result-body .item:nth-child(odd) {
            margin-right: 5px; }
          .search-container .search-results .search-result .result-body .item:hover {
            background-color: #232323; }
          .search-container .search-results .search-result .result-body .item:active {
            opacity: 0.7;
            height: 55px;
            width: 47%;
            margin-top: 20px;
            margin-left: 5px;
            margin-right: 5px; }
            .search-container .search-results .search-result .result-body .item:active .image img {
              height: 55px;
              width: 55px; }
            .search-container .search-results .search-result .result-body .item:active .details h4 {
              font-size: 14px; }
            .search-container .search-results .search-result .result-body .item:active .details span {
              font-size: 12px; }
          .search-container .search-results .search-result .result-body .item .image i {
            position: absolute;
            -webkit-text-stroke: 1px black;
            margin-left: -42px;
            margin-top: 14px;
            font-size: 30px;
            display: none; }
            .search-container .search-results .search-result .result-body .item .image i:hover {
              display: inline-block;
              margin-left: -43px;
              margin-top: 13px;
              font-size: 33px; }
          .search-container .search-results .search-result .result-body .item .image img {
            height: 60px;
            width: 60px; }
          .search-container .search-results .search-result .result-body .item .image:hover i {
            display: inline; }
          .search-container .search-results .search-result .result-body .item .image:hover img {
            opacity: 0.6; }
          .search-container .search-results .search-result .result-body .item .details {
            width: 100%;
            height: 60px;
            padding-left: 10px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis; }
            .search-container .search-results .search-result .result-body .item .details h4 {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              margin-top: 10px;
              margin-bottom: 5px;
              font-size: 15px; }
            .search-container .search-results .search-result .result-body .item .details span {
              color: #ccc;
              font-size: 13px; }
          .search-container .search-results .search-result .result-body .item.artist img {
            border-radius: 30px; }
          .search-container .search-results .search-result .result-body .item.artist h4 {
            margin-top: 22px; }
