.albums-title {
  border-bottom: 1px solid #6666665c;
  padding-bottom: 10px; }

.albums-container {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  padding: 0;
  justify-content: space-evenly; }

.album {
  padding: 0px 20px;
  padding-bottom: 40px;
  width: 200px;
  cursor: default; }
  .album h4 {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 20px; }
  .album .album-header {
    display: block; }
  .album img {
    display: inline;
    height: 180px; }
    .album img:hover {
      opacity: 0.7; }
    .album img:active {
      opacity: 0.5; }
  .album .album-info {
    position: relative; }
    .album .album-info .album-year {
      color: #ccc;
      font-size: 13px; }
    .album .album-info .album-title {
      font-size: 14px;
      text-overflow: hide;
      overflow: hidden;
      margin-top: 5px;
      margin-bottom: 5px; }
