.current-artist-header-container {
  display: flex; }
  .current-artist-header-container .current-artist-image {
    width: 100%;
    height: 400px;
    object-fit: cover;
    object-position: 50% 25%;
    opacity: 0.7; }
  .current-artist-header-container .current-artist-info {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 20px;
    top: 185px; }
    .current-artist-header-container .current-artist-info h3 {
      font-size: 70px;
      margin-bottom: 0px; }
    .current-artist-header-container .current-artist-info p {
      text-transform: uppercase;
      font-family: 'Proxima Thin', Georgia, sans-serif;
      font-size: 13px; }
  @media (max-width: 900px) {
    .current-artist-header-container .current-artist-image {
      height: 300px; }
    .current-artist-header-container .current-artist-info {
      top: 150px; }
      .current-artist-header-container .current-artist-info h3 {
        font-size: 45px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 500px; }
      .current-artist-header-container .current-artist-info .menu-btn {
        display: none; } }
  @media (max-width: 600px) {
    .current-artist-header-container .current-artist-info h3 {
      width: 275px; } }
