.side-menu-container {
  padding: 20px;
  color: #b3b3b3;
  margin: 0; }
  .side-menu-container .menu-item {
    list-style-type: none;
    line-height: 28px;
    font-size: 14px;
    cursor: pointer;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 170px; }
    .side-menu-container .menu-item:hover {
      color: #fff; }
    .side-menu-container .menu-item.active:before {
      content: "";
      position: relative;
      left: -18px;
      border-left: 3px solid #1db954; }
  .side-menu-container .library-header {
    font-weight: 300;
    text-transform: uppercase;
    font-family: "Proxima Thin", Georgia, sans-serif;
    font-size: 12px;
    margin: 0;
    padding: 30px 0 5px 0;
    letter-spacing: 1px; }
  .side-menu-container .radio {
    margin-bottom: 20px; }
  .side-menu-container .user-playlist-container {
    padding-bottom: 320px;
